.login-page-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: calc(100vh - var(--header-height) - var(--footer-height)); /* Adjust for header/footer */
    background-color: var(--color-background-light);
    padding: var(--spacing-xl);
}

.login-form-card {
    background-color: var(--color-white);
    padding: var(--spacing-xxl);
    border-radius: var(--border-radius-medium);
    box-shadow: var(--shadow-medium);
    width: 100%;
    max-width: 450px;
    text-align: center;
}

.login-form-card h2 {
    color: var(--color-primary);
    margin-bottom: var(--spacing-xl);
    font-size: var(--font-size-h3);
}

.login-form-card form {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
}

.login-form-card .input-group {
    margin-bottom: var(--spacing-sm);
    text-align: left; /* Align labels and inputs */
}

.form-error-message {
    color: var(--color-error);
    font-size: var(--font-size-small);
    margin-top: var(--spacing-xs);
    margin-bottom: var(--spacing-md);
    text-align: center;
}

.login-form-card .button {
    width: 100%;
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-large);
    margin-top: var(--spacing-lg);
}

.register-link-text {
    margin-top: var(--spacing-lg);
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
}

.register-link-text a {
    color: var(--color-secondary);
    text-decoration: none;
    font-weight: 600;
}

.register-link-text a:hover {
    text-decoration: underline;
}

