.button {
    padding: var(--spacing-sm) var(--spacing-md);
    border: none;
    border-radius: var(--border-radius-small);
    cursor: pointer;
    font-size: var(--font-size-base);
    font-weight: 600;
    transition: var(--transition-ease);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: var(--spacing-xxs);
}

/* Variants */
.button--primary {
    background-color: var(--color-primary);
    color: var(--color-white);
}

.button--primary:hover:not(.button--disabled) {
    background-color: var(--color-primary-dark);
}

.button--secondary {
    background-color: var(--color-secondary);
    color: var(--color-white);
}

.button--secondary:hover:not(.button--disabled) {
    background-color: var(--color-secondary-dark);
}

.button--danger {
    background-color: var(--color-error);
    color: var(--color-white);
}

.button--danger:hover:not(.button--disabled) {
    background-color: #C82333; /* Darker red */
}

.button--outline {
    background-color: transparent;
    color: var(--color-primary);
    border: 2px solid var(--color-primary);
}

.button--outline:hover:not(.button--disabled) {
    background-color: var(--color-primary-dark);
    color: var(--color-white);
}

/* Sizes */
.button--small {
    padding: var(--spacing-xs) var(--spacing-sm);
    font-size: var(--font-size-small);
}

.button--medium {
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-base);
}

.button--large {
    padding: var(--spacing-md) var(--spacing-lg);
    font-size: var(--font-size-large);
}

/* Disabled state */
.button--disabled {
    background-color: var(--color-medium-gray);
    color: var(--color-text-secondary);
    cursor: not-allowed;
    opacity: 0.7;
}`, "",{"version":3,"sources":["webpack://./src/components/common/Button/Button.css"],"names":[],"mappings":"AAAA,wCAAwC;AACxC;IACI,4CAA4C;IAC5C,YAAY;IACZ,yCAAyC;IACzC,eAAe;IACf,gCAAgC;IAChC,gBAAgB;IAChB,kCAAkC;IAClC,oBAAoB;IACpB,mBAAmB;IACnB,uBAAuB;IACvB,uBAAuB;AAC3B;;AAEA,aAAa;AACb;IACI,sCAAsC;IACtC,yBAAyB;AAC7B;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,wCAAwC;IACxC,yBAAyB;AAC7B;;AAEA;IACI,6CAA6C;AACjD;;AAEA;IACI,oCAAoC;IACpC,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB,EAAE,eAAe;AAC9C;;AAEA;IACI,6BAA6B;IAC7B,2BAA2B;IAC3B,sCAAsC;AAC1C;;AAEA;IACI,2CAA2C;IAC3C,yBAAyB;AAC7B;;AAEA,UAAU;AACV;IACI,4CAA4C;IAC5C,iCAAiC;AACrC;;AAEA;IACI,4CAA4C;IAC5C,gCAAgC;AACpC;;AAEA;IACI,4CAA4C;IAC5C,iCAAiC;AACrC;;AAEA,mBAAmB;AACnB;IACI,0CAA0C;IAC1C,kCAAkC;IAClC,mBAAmB;IACnB,YAAY;AAChB","sourcesContent":["/* components/common/Button/Button.css */\n.button {\n    padding: var(--spacing-sm) var(--spacing-md);\n    border: none;\n    border-radius: var(--border-radius-small);\n    cursor: pointer;\n    font-size: var(--font-size-base);\n    font-weight: 600;\n    transition: var(--transition-ease);\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    gap: var(--spacing-xxs);\n}\n\n/* Variants */\n.button--primary {\n    background-color: var(--color-primary);\n    color: var(--color-white);\n}\n\n.button--primary:hover:not(.button--disabled) {\n    background-color: var(--color-primary-dark);\n}\n\n.button--secondary {\n    background-color: var(--color-secondary);\n    color: var(--color-white);\n}\n\n.button--secondary:hover:not(.button--disabled) {\n    background-color: var(--color-secondary-dark);\n}\n\n.button--danger {\n    background-color: var(--color-error);\n    color: var(--color-white);\n}\n\n.button--danger:hover:not(.button--disabled) {\n    background-color: #C82333; /* Darker red */\n}\n\n.button--outline {\n    background-color: transparent;\n    color: var(--color-primary);\n    border: 2px solid var(--color-primary);\n}\n\n.button--outline:hover:not(.button