// src/store/features/auth/authSlice.js
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import apiService from '../../../api/apiService'; // Import your API service

// Async Thunk for login
export const loginUser = createAsyncThunk(
    'auth/loginUser',
    async ({ usernameOrEmail, password }, { rejectWithValue }) => {
        try {
            // Parameter name now correctly matches apiService.login
            const response = await apiService.login(usernameOrEmail, password);
            // Assuming your backend returns a token and user info
            localStorage.setItem('jwtToken', response.data.token);
            return response.data;
        } catch (error) {
            // More robust error handling for rejectWithValue
            const errorMessage = error.response && error.response.data
                ? error.response.data // Assuming backend sends { message: "..." } or similar
                : { message: error.message || 'An unexpected error occurred.' };
            return rejectWithValue(errorMessage);
        }
    }
);

const authSlice = createSlice({
    name: 'auth',
    initialState: {
        user: null,
        token: localStorage.getItem('jwtToken') || null,
        isAuthenticated: !!localStorage.getItem('jwtToken'),
        status: 'idle', // 'idle' | 'loading' | 'succeeded' | 'failed'
        error: null, // Stores error object, e.g., { message: 'Invalid credentials.' }
    },
    reducers: {
        logout: (state) => {
            state.user = null;
            state.token = null;
            state.isAuthenticated = false;
            state.status = 'idle'; // Reset status on logout
            state.error = null; // Clear any errors on logout
            localStorage.removeItem('jwtToken');
        },
        // Other synchronous actions
    },
    extraReducers: (builder) => {
        builder
            .addCase(loginUser.pending, (state) => {
                state.status = 'loading';
                state.error = null; // Clear any previous errors on new attempt
            })
            .addCase(loginUser.fulfilled, (state, action) => {
                state.status = 'succeeded';
                state.user = action.payload.user; // Assuming user data is in payload
                state.token = action.payload.token;
                state.isAuthenticated = true;
                state.error = null; // Clear error on success
            })
            .addCase(loginUser.rejected, (state, action) => {
                state.status = 'failed';
                state.error = action.payload; // action.payload will contain the error from rejectWithValue
                state.isAuthenticated = false; // Ensure isAuthenticated is false on rejection
                state.user = null; // Clear user data on rejection
                state.token = null; // Clear token on rejection
                localStorage.removeItem('jwtToken'); // Remove token from local storage on rejection
            });
    },
});

export const { logout } = authSlice.actions;
export default authSlice.reducer;