/* styles/variables.css */
/* Defines CSS variables for consistent styling across the application */

:root {
    /* Colors */
    --color-primary: #4CAF50; /* Green */
    --color-primary-rgb: 76, 175, 80; /* RGB components of #4CAF50 */
    --color-primary-dark: #388E3C;
    --color-secondary: #1976D2; /* Blue */
    --color-secondary-dark: #1565C0;
    --color-accent: #FFC107; /* Amber */
    --color-accent-dark: #FFB300;

    --color-text-primary: #333333;
    --color-text-secondary: #666666;
    --color-white: #FFFFFF;
    --color-light-gray: #EEEEEE;
    --color-medium-gray: #CCCCCC;
    --color-dark-gray: #555555;
    --color-background-light: #F8F9FA; /* Light background for pages */
    --color-success: #28A745;
    --color-error: #DC3545;
    --color-error-rgb: 220, 53, 69;
    --color-warning: #FFC107;
    --color-info: #17A2B8;




    /* Typography */
    --font-family-primary: 'Inter', sans-serif;
    --font-size-base: 1rem;
    --font-size-small: 0.875rem;
    --font-size-large: 1.125rem;
    --font-size-h1: 2.5rem;
    --font-size-h2: 2rem;
    --font-size-h3: 1.75rem;
    --font-size-h4: 1.5rem;
    --font-size-h5: 1.25rem;
    --font-size-h6: 1rem;

    /* Spacing */
    --spacing-xxs: 0.25rem;
    --spacing-xs: 0.5rem;
    --spacing-sm: 0.75rem;
    --spacing-md: 1rem;
    --spacing-lg: 1.5rem;
    --spacing-xl: 2rem;
    --spacing-xxl: 3rem;

    /* Border Radius */
    --border-radius-small: 4px;
    --border-radius-medium: 8px;
    --border-radius-large: 12px;
    --border-radius-circle: 50%;

    /* Shadows */
    --shadow-small: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    --shadow-medium: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    --shadow-large: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    --shadow-small-inset: inset 0 1px 3px rgba(0, 0, 0, 0.12), inset 0 1px 2px rgba(0, 0, 0, 0.24);

    /* Transitions */
    --transition-ease: all 0.3s ease-in-out;


    /* Header and Footer */
    --header-height: 4rem;
    --footer-height: 5rem;

}
