// ... (all other page imports) ...

// PrivateRoute component (no change here, but it will now correctly access context)
const PrivateRoute = ({ children, allowedRoles }) => {
    const isAuthenticated = useSelector(selectIsAuthenticated);
    // You might also get user roles from Redux state: const userRole = useSelector(selectUserRole);

    if (!isAuthenticated) {
        return <Navigate to="/login" replace />;
    }
    // ...
    return children;
};

