___CSS_LOADER_EXPORT___.push([module.id, `/* components/common/Input/Input.css */
.input-group {
    margin-bottom: var(--spacing-md);
}

.input-label {
    display: block;
    margin-bottom: var(--spacing-xs);
    font-size: var(--font-size-base);
    color: var(--color-text-primary);
    font-weight: 500;
}

.input-field {
    width: 100%;
    padding: var(--spacing-sm);
    border: 1px solid var(--color-medium-gray);
    border-radius: var(--border-radius-small);
    font-size: var(--font-size-base);
    color: var(--color-text-primary);
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.input-field:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 2px rgba(var(--color-primary-rgb), 0.2); /* Assuming RGB for primary color */
}

.input-field::placeholder {
    color: var(--color-text-secondary);
    opacity: 0.7;
}

.input-field--error {
    border-color: var(--color-error);
}

.input-field--error:focus {
    box-shadow: 0 0 0 2px rgba(var(--color-error-rgb), 0.2);
}

.input-error-message {
    color: var(--color-error);
    font-size: var(--font-size-small);
    margin-top: var(--spacing-xxs);
}

/* Adjust for disabled/read-only states */
.input-field:disabled,
.input-field[readonly] {
    background-color: var(--color-light-gray);
    cursor: not-allowed;
    opacity: 0.8;
}
